.products {
  position: relative;
  z-index: 5;
  padding-top: 121px;
  padding-bottom: 98px;
  background-color: getColor('text-color-light');
  @media screen and (min-width: 768px) {
    padding-bottom: 112px;
  }
  @media screen and (min-width: 1280px) {
    padding-top: 127px;
    padding-bottom: 119px;
  }
}

.products__label {
  margin-bottom: 15px;
  font-size: 18px;
  text-align: center;
  font-family: getFF(brand-font);
  text-transform: uppercase;
  color: getColor(brand-color-light);

  @media screen and (min-width: 768px) {
    font-size: 26px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 30px;
  }
}

.products__title {
  margin-bottom: 144px;
  font-size: 34px;
  text-align: center;
  font-family: getFF(brand-font);
  text-transform: uppercase;
  color: getColor(brand-color-dark);

  @media screen and (min-width: 768px) {
    margin-bottom: 150px;
    font-size: 48px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 260px;
    font-size: 58px;
  }
}

.products__list {
  @media screen and (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    margin: -10px;
  }
  @media screen and (min-width: 1280px) {
    margin: -12px;
  }
}

.products__product-card {
  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: 768px) {
    margin: 10px;
  }

  @media screen and (min-width: 1280px) {
    margin: 12px;
  }

  &:not(:last-child) {
    @media screen and (max-width: 767px) {
      margin-bottom: 130px;
    }
  }
}

.product-card {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 161px 41px 41px 41px;

  border-radius: 24px;

  @media screen and (max-width: 767px) {
    max-width: 280px;
  }

  @media screen and (min-width: 768px) {
    padding: 152px 11px 41px 11px;
    width: calc((100% - 3 * 20px) / 3);
  }

  @media screen and (min-width: 1280px) {
    padding: 162px 44px 50px 44px;
    width: calc((100% - 3 * 24px) / 3);
  }
}
.product-card::before {
  position: absolute;
  top: -113px;
  content: '';
  display: block;
  width: 249px;
  height: 252px;
  background-repeat: no-repeat;
  background-size: contain;
background-position:center;
  @media screen and (min-width: 768px) {
    top: -102px;
    width: 100%;
    height: 226px;
  }

  @media screen and (min-width: 1280px) {
    top: -210px;
    width: 100%;
    height: 335px;
  }
}
.product-card:first-child {
  background-color: #ffb8ca;

  &::before {
    background-image: url('/images/products/products-1-mobile.png');

    @media screen and (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url('/images/products/products-1-mobile@2x.png');
    }
    @media screen and (min-width: 768px) {
      background-image: url('/images/products/products-1-tablet.png');

      @media screen and (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: url('/images/products/products-1-tablet@2x.png');
      }
    }
    @media screen and (min-width: 1280px) {
      background-image: url('/images/products/products-1-desktop.png');

      @media screen and (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: url('/images/products/products-1-desktop@2x.png');
      }
    }
  }
}

.product-card:nth-child(2) {
  background-color: #f0d1a5;

  &::before {
    background-image: url('/images/products/products-2-mobile.png');

    @media screen and (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url('/images/products/products-2-mobile@2x.png');
    }
    @media screen and (min-width: 768px) {
      background-image: url('/images/products/products-2-tablet.png');

      @media screen and (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: url('/images/products/products-2-tablet@2x.png');
      }
    }
    @media screen and (min-width: 1280px) {
      background-image: url('/images/products/products-2-desktop.png');

      @media screen and (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: url('/images/products/products-2-desktop@2x.png');
      }
    }
  }
}

.product-card:last-child {
  background-color: #c2e297;

  &::before {
    background-image: url('/images/products/products-3-mobile.png');

    @media screen and (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url('/images/products/products-3-mobile@2x.png');
    }
    @media screen and (min-width: 768px) {
      background-image: url('/images/products/products-3-tablet.png');

      @media screen and (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: url('/images/products/products-3-tablet@2x.png');
      }
    }
    @media screen and (min-width: 1280px) {
      background-image: url('/images/products/products-3-desktop.png');

      @media screen and (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: url('/images/products/products-3-desktop@2x.png');
      }
    }
  }
}

.product-card__title {
  font-size: 20px;
  font-family: getFF(brand-font);
  text-transform: uppercase;
  color: getColor(text-color-light);

  @media screen and (min-width: 1280px) {
    font-size: 30px;
    font-weight: 700;
  }
}

.product-card__title::after {
  content: '';
  margin: 30px auto;
  display: block;
  width: 26px;
  height: 6px;
  background-image: url('/images/products/points.png');
  background-size: cover;

  @media screen and (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image: url('/images/products/points@2x.png');
  }

  @media screen and (min-width: 1280px) {
    margin: 32px auto;
  }
}

.product-card__text {
  margin-bottom: 30px;
  font-size: 14px;
  font-weight: 700;
  text-align: center;
  color: getColor(text-color-light);
  font-family: getFF(description-font);

  @media screen and (min-width: 768px) {
    margin-bottom: 40px;
    height: 124px;
    overflow: hidden;
    font-size: 14px;
    font-weight: 700;
  }

  @media screen and (min-width: 1280px) {
    margin-bottom: 62px;
    height: 106px;
    font-size: 16px;
    font-weight: 700;
  }
}

.product-card__button {
  background-color: map-get($color, text-color-light);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  display: block;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  &:hover,
  &:focus {
    background-color: getColor(brand-color-dark);
  }
}

.product-card__icon {
  fill: getColor(brand-color-dark);
  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);

  .product-card__button:hover &,
  .product-card__button:focus & {
    fill: getColor(text-color-light);
  }
}
